import shortid from "shortid";

export default function seed(store) {
    console.log("Insert first list");
    const firstListId = shortid.generate();

    store.dispatch({
        type: "ADD_LIST",
        payload: { listId: firstListId, listTitle: "Click here to edit and delete list" }
    });

    store.dispatch({
        type: "ADD_CARD",
        payload: {
            listId: firstListId,
            cardId: shortid.generate(),
            cardText: "Text 1",
            cardTitle: "Title 1"
        }
    });

    store.dispatch({
        type: "ADD_CARD",
        payload: {
            listId: firstListId,
            cardId: shortid.generate(),
            cardText: "Second card",
            cardTitle: "Title 2"
        }
    });

    console.log("Insert second list");
    const secondListId = shortid.generate();

    store.dispatch({
        type: "ADD_LIST",
        payload: { listId: secondListId, listTitle: "Second list" }
    });

    store.dispatch({
        type: "ADD_CARD",
        payload: {
            listId: secondListId,
            cardId: shortid.generate(),
            cardText: "Card 1",
            cardTitle: "Title 1"
        }
    });

    store.dispatch({
        type: "ADD_CARD",
        payload: {
            listId: secondListId,
            cardId: shortid.generate(),
            cardText: "Card 2",
            cardTitle: "Title 2"
        }
    });
};
