import "../styles/ListEditor.css";

import React, { Component } from "react";
import TextareaAutosize from "react-textarea-autosize";

class ListEditor extends Component {
  ref = React.createRef();

  state = {
    toogleEditing: false
  };

  onEnter = e => {
    if (e.keyCode === 13) {
      e.preventDefault();
      this.setState({toogleEditing : false});
      this.props.saveList();
    }
  };

  handleClick = e => {
    const node = this.ref.current;
    console.log(node, e);
    if (node.contains(e.target)) {
      console.log("return from edit");
      return;
    }
    const {toogleEditing} = this.state;
    if (!toogleEditing){
      this.setState({toogleEditing : true});
    } else {
      this.setState({toogleEditing : false});
      this.props.onClickOutside();
    }
  };

  componentDidMount() {
    console.log("editlist");
    document.addEventListener("click", this.handleClick, false);
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.handleClick, false);
  }

  render() {
    const { title, handleChangeTitle, deleteList } = this.props;

    return (
      <div className="List-Title-Edit" ref={this.ref}>
        <TextareaAutosize
          autoFocus
          className="List-Title-Textarea"
          placeholder="Enter list title..."
          value={title}
          onChange={handleChangeTitle}
          onKeyDown={this.onEnter}
          style={{ width: deleteList ? 220 : 245 }}
        />
        <div className="Card-Icon" onClick={deleteList}>
          <ion-icon name="trash"  />
        </div>
      </div>
    );
  }
}

export default ListEditor;
